                        {searchTab == 0 && subSearchTab == 1 && (<SellAgricultureFarmHouseComponent />)}

                        {searchTab == 1 && subSearchTab == 0 && (<SellResidentialOpenPlotsComponent />)}
                        {searchTab == 1 && subSearchTab == 1 && (<SellResidentialOpenFlatsComponent />)}
                        {searchTab == 1 && subSearchTab == 2 && (<SellResidentialOpenVillasComponent />)}

                        {searchTab == 2 && subSearchTab == 0 && (<SellCommercialOpenSpaces />)}
