import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";


import Logo from "../../assets/images/logo.svg";
import ProfilePic from "../../assets/images/profile.jpg";


const Header = (props) => {
  const { currentPage } = props;
  const navigate = useNavigate();
  window.scrollTo(0, 0);

  const [showDropdown, setDropdown] = useState(false);
  const [showMobileDropdown, setMobileDropdown] = useState(false);



  const handleDropdown = () => {
    setDropdown(!showDropdown);
  }

  const handleMobileDropdown = () => {
    setMobileDropdown(!showMobileDropdown);
  }




  return (
    <nav className="navbar navbar-inverse navbar-fixed-top">
      <div className="container-fluid">
        <div className="navbar-header">
          <button type="button" className="navbar-toggle collapsed" onClick={handleMobileDropdown}> <span className="sr-only">Toggle navigation</span> <span className="icon-bar"></span> <span className="icon-bar"></span> <span className="icon-bar"></span> </button>

          <a className="navbar-brand"><img src={Logo} alt="logo" /></a> </div>


        <div className="navbar-collapse collapse in" id="bs-example-navbar-collapse-1" style={{ display: showMobileDropdown ? "block" : "none" }}>
          <ul className="nav navbar-nav navbar-right">
            <li onClick={() => navigate("/")} className={currentPage == "home" ? "active" : ""}><a role="button">Home</a></li>
            <li onClick={() => navigate("/buy")} className={currentPage == "buy" ? "active" : ""}><a role="button">Buy</a></li>
            <li onClick={() => navigate("/sell")} className={currentPage == "sell" ? "active" : ""}><a role="button">Sell</a></li>
            <li><a role="button">Title Verification Service</a></li>
            <li><a role="button">About us</a></li>
            <li className="nav-icon"><a role="button"><i className="glyphicon glyphicon-bell hidden"></i><i className="zmdi zmdi-eye"></i> <span className="badge">4</span></a></li>
            <li className="nav-icon"><a role="button"><i className="zmdi zmdi-notifications"></i> <span className="badge">3</span></a></li>
            <li className="nav-icon"><a role="button"><i className="glyphicon glyphicon-heart"></i></a></li>
            <li className="dropdown user-profile"> <a onClick={handleDropdown} role="button" style={{ backgroundColor: showDropdown ? "#080808" : "transparent" }}>
              <img src={ProfilePic} width="100" height="100" alt="Profile img" />Srinivas <span className="caret"></span></a>
              <ul className="dropdown-menu" style={{ display: showDropdown ? "block" : "none" }}>
                <li><a role="button">Profile</a></li>
                <li onClick={() => navigate("/account")}><a role="button">My Properties</a></li>
                <li><a role="button">Settings</a></li>
                <li role="separator" className="divider"></li>
                <li><a role="button">Logout</a></li>
              </ul>
            </li>
          </ul>
        </div>

      </div >
    </nav >
  );
};

export default Header;